nav {
    background-color: black;
    border-bottom: 1px solid white;
}

/* Links */

/* telefone e redes sociais */
.nav-social-icons {
    padding: 0px;
    margin: 0px;
    font-size: 22px;
}
/* redes sociais font size */
.nav-sociais a {
    font-size: 35px;
}

/* estilo dos links da navbar */
nav ul a:hover {
    color: white;
    border-radius: .25rem;
}
/* retira as bordas dos links na versao mobile */
@media only screen and (min-width: 992px) {
    nav ul a:hover {
        background-color: white;
        color: black;
    }
}
nav ul a:active {
    margin-top: 2px;
    color: black;
    border-radius: .25rem;
}
/* coloca margin top nos links quando os links estão retraídos */
@media only screen and (max-width: 991px) {
    .navbar-nav {
        margin-top: 10px;
    }
}

/* configuração do dropdown menu e links*/
nav .dropdown-menu {
    background-color: black;
    border: 1px solid white;
}

nav .dropdown-item {
    color: white;
}

/* navbar inputs (desativado) */
nav form input {
    max-width: 150px;
}
@media only screen and (max-width: 986px) {
    nav form input {
        max-width: 1000px;
    }
}















