@import './Navbar.css';
@import "./Carousel.css";
@import "./Principal.css";
@import './Footer.css';

/* Configurações gerais */
body {
    margin: 0px;
    padding: 0px;
    overflow-x: hidden;
    color: white;
    background-color: black;
}

a {
    color: white;
}

a:hover {
    color: rgb(211, 201, 201);
    text-decoration: none;
}

/* classes */
.mt-150 {
    margin-top: 150px;
}
/* Buttons */
.btn-gradient {
    padding: 7px 25px;
    color: white !important;
    background-image: linear-gradient(to right bottom, #0ecdf0, #00bbfc, #00a6ff, #008dff, #006fff, #005dfd, #1e49f8, #362ef1, #452be7, #5028dd, #5725d3, #5d23c9);
}

.btn-gradient:hover {
    background-image: linear-gradient(to right bottom, rgba(14,205,240,1) 0%, rgba(93,35,201,1) 100%);
}
