/* MAIN */

/* Modal */
.modal-content {
    background-color: black;
}
/* redes sociais links */
.modal-content a {
        font-size: 42px;
        padding: 0px 8px;
}
/* alinha os headers */
.my-modal-header {
    /* text-align: initial; */
    padding: 5px 0px 0px 15px;
}
/* separa os títulos com borda branca */
.my-modal-content {
    padding: 5px 0px;
    border-bottom: 1px solid white;
}
/* alinha o link do modal à direita */
.my-modal-footer {
    display: flex;
    padding: 1rem;
    justify-content: flex-end;
}



/* Cursos */
.card {
    background-color: black;
    border: 1px solid white;
    margin-right: 3px;
    margin-top: 3px;
    padding: 1px;
}

.card a {
    align-self: flex-end;
}
